
// 是不是仅读的，仅读的属性set时会报异常
// 是不是深度的

import { extend, hasChanged, hasOwn, isArray, isIntegerKey, isObject } from "@vue/shared";
import { track, trigger } from "./effect";
import { readonly, reactive } from "./reactive";
import { TrackOpTypes, TriggerOpTypes } from "./operators";

function createGetter(isReadonly = false, shallow = false) { // 拦截获取功能
    return function get(target, key, receiver) {
        // 后续Object上的方法会迁移到Reflect上
        // 以前target[key] = value 方式设置值可能会失败，并不会报异常，也没有返回值标识
        // Reflect方法具备返回值
        
        const res = Reflect.get(target, key, receiver);

        if (!isReadonly) {
            // 收集依赖，等会数据变化后更新对应的视图
            // console.log("执行effect的时候会取值，收集effect");
            track(target, TrackOpTypes.GET, key);
        }

        if (shallow) {
            return res;
        }

        if (isObject(res)) { // vue2是一上来就递归，vue3是当取值时进行代理。懒代理
            return isReadonly ? readonly(res) : reactive(res);
        }
        return res;
    }
}

function createSetter(shallow = false) { // 拦截设置功能
    return function set(target, key, value, receiver) {
        const oldValue = target[key]; // 取老值

        let hadKey = isArray(target) && isIntegerKey(key) ? Number(key) < target.length : hasOwn(target, key);

        const result = Reflect.set(target, key, value, receiver);

        // 我们要区分是【新增】还是【修改】，vue2中无法监控更改索引，无法监控数组的长度，vue3中可以，hack方法，需要特殊处理
        if (!hadKey) {
            // 新增
            trigger(target, TriggerOpTypes.ADD, key, value);
        } else if (hasChanged(oldValue, value)) {
            // 修改
            trigger(target, TriggerOpTypes.SET, key, value, oldValue);
        }

        // 数据更新时，通知对应属性的effect重新执行
    }
}

const get = createGetter();
const shallowGet = createGetter(false, true);
const readonlyGet = createGetter(true, false);
const shallowReadonlyGet = createGetter(true, true);

const set = createSetter();
const shallowSet = createSetter(true);

export const mutableHandlers = {
    get,
    set
}
export const shallowReactiveHandlers = {
    get: shallowGet,
    set: shallowSet
}

let readonlyObj = {
    set: (target, key) => {
        console.warn(`set on key ${key} failed`);
    }
}

export const readonlyHandlers = extend({
    get: readonlyGet
}, readonlyObj);


export const shallowReadonlyHandlers = extend({
    get: shallowReadonlyGet
}, readonlyGet);